body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e1e1e1;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  overflow: hidden;
}
.app {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #F5F5F6;
}
.title,
.controls,
.main {
  width: 100%;
  display: flex;
  justify-content: center;
}
.title {
  background-color: #98aa00;
}
.controls__container,
.main {
  width: auto;
}
.title__container {
  padding: 0px 15px;
}
.title__text {
  font-weight: 500;
  font-size: 2.125rem;
  letter-spacing: -1px;
}
.controls {
  flex-direction: column;
  align-items: center;
  background-color: #ccdb37;
}
.controls__container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  align-self: baseline;
  padding-left: 20px;
  background-color: #ccdb37;
}
.controls__title {
  font-weight: 400;
  font-size: 1.5rem;
  letter-spacing: 0;
  margin-top: 0.83rem;
  padding-bottom: 5px;
}
.controls__item {
  padding: 10px 0px;
}
.controls__text {
  display: inline-block;
  padding-right: 5px;
  text-align: left;
  font-weight: 500;
  font-size: 1rem;
  letter-spacing: 1px;
  width: 50px;
  min-height: 18px;
}
.controls__select {
  margin-right: 20px;
  padding: 2px 10px;
  display: inline-block;
  -moz-appearance: none;
  -webkit-appearance: none;
  border-radius: 4px;
}
.controls__select:hover {
  border: 1px solid #455a64;
  background-color: #66a5c3;
}
.controls__button {
  background-color: #455a64;
  color: #ffffff;
  width: 55px;
  height: 24px;
  border-radius: 4px;
  border: 0 solid transparent;
}
.controls__button:hover {
  background-color: #66a5c3;
}
.main {
  width: 100%;
  background-color: #F5F5F6;
  display: flex;
  justify-content: center;
}
.exchange {
  margin-top: 20px;
  width: 600px;
  display: flex;
  justify-content: center;
  align-content: center;
}
.exchange__table {
  border-collapse: collapse;
  border: 10px solid transparent;
  width: 416px;
  height: auto;
}
.exchange__row-header {
  display: table-header-group;
  color: #000000;
  font-weight: 600;
  letter-spacing: 2px;
  font-size: 1rem;
}
.exchange__row-data {
  display: table-row;
  color: #333333;
  font-weight: 400;
  border-bottom: 1px solid #98aa00;
}
.exchange__row-data:hover {
  border-bottom-color: #ccdb37;
  text-decoration: line-through;
  cursor: pointer;
}
.exchange__cell {
  display: table-cell;
  padding: 5px;
  text-align: center;
  border: inherit;
}
